  .ultext li{
    width: 700px;
    height: 135px;
   margin-bottom: 30px;
   padding: 0 30px;
  }
  .ultext .bwite-top{
    width: 650px;
    height: 73px;
    display: flex;justify-content: space-between;
  }
  /* .ultext {
    display: flex;
  } */
  .ultext .img{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    margin-right: 10px;
  }
  .ultext .img img{
    border-radius: 50%;
  }
  .ultext .img span{
    display: inline-block;
    width: 40px;
    height: 40px;
    text-align: center;
    background-color: #2486e4;
    color: #fff;
    line-height: 40px;
    border-radius: 50%;
  }
  .ultext .txt p:nth-child(1) {
    margin-bottom: 5px;
  }
  .ultext .txt p:nth-child(1) span{
    color: #999;font-size: 14px;margin-left: 10px;
  }
  .ultext .txt p:nth-child(2){
    font-size: 12px;color: #999;
  }
  .ultext .bwite-rft{
    float: right;
    margin-right: -35px;
    font-size: 14px;
  }
  .ultext .bwite-rft i{
    color: darkorange;font-size: 20px;
  }
  .ultext .bwite-btom{
    padding: 0 20px;
  }
  .ultext .bwite-btom p{
    background-color: #f4f7fd;line-height: 38px;padding: 0 8px;color: #3e84e9;
  }
  .litsl{
    border: solid 1px #e6e5e5;
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
   
  }
  .litsl .bwite-top{
      display: block;
  }
  .ite-com p{
    color: #999;
  }
  .ite-com span{
    color: #000;
  }
  .litsl .bwite-top{
    padding: 20px 20px 5px;
  }
  .litsl  .bwitem{
    border-bottom: solid 1px #e6e5e5;
  }
  .litsl .reply{
    background-color: #f4f7fd;text-align: right;padding: 5px 30px;
  }
  .litsl .reply button{
    background-color: #1890ff;
    color: #fff;
    border-radius: 4px;
    -webkit-border-radius: 4px;
    -moz-border-radius: 4px;
    -ms-border-radius: 4px;
    -o-border-radius: 4px;
    padding: 0 10px;
    height: 36px;
    line-height: 36px;
  }
  .litsl .reply button i{
    padding: 0;margin-right: 5px;
  }
  .litsl .inputs{
    padding: 20px;
  }
  .litsl .inpt-box{
    border: solid 1px #e6e5e5;padding: 10px;
  }
  .litsl .inpt{
    width: 100%;height: 30px;margin-bottom: 20px;
  }
  .litsl .inpt input{
    width: 100%;padding: 0 20px;height: 100%;color: #888;
  }
  .litsl .ite-bts{
    display: flex;justify-content: space-between;
  }
  .litsl .ite-bts span{
    color: #999;font-size: 20px;
  }
  .litsl .ite-bts .btnn button{
    padding: 0 16px;
    height: 34px;
    line-height: 34px;
    border-radius: 4px;
    -webkit-border-radius: 4px;
    -moz-border-radius: 4px;
    -ms-border-radius: 4px;
    -o-border-radius: 4px;
    font-size: 14px;
    margin: 0 10px;
  }
  .litsl .ite-bts .btnn button:nth-child(1){
    background-color: #3e84e9;color: #fff;
  }
  .litsl .ite-bts .btnn button:nth-child(2){
    border: solid 1px #ccc;
  }
