  }
  .ri-title{
    display: flex;justify-content: space-between;line-height: 44px;border-bottom: solid 1px #ccc;height: 45px;
  }
  .ri-title p:nth-child(2){
    color: #2486e4;
  }
  .off-list li,.off-list li i{
    font-size: 16px !important;
  }
  .bworoff{
    overflow: hidden;height: 700px;
  }
  .bworoff ul{
    overflow-y: auto;height: 1400px;
  }
  .bworoff ul::-webkit-scrollbar {
    width: 12px;height: 30px;
  }
  /* 滚动槽 */
  .bworoff ul::-webkit-scrollbar-track {
    
    border-radius: 10px;
  }
  /* 滚动条滑块 */
  .bworoff ul::-webkit-scrollbar-thumb {
    border-radius: 10px;
    background: rgba(0, 0, 0, 0.1);
  }
