.demo2 .no1_2_box{
    width: 100%;
    background: #fff;
    padding: 15px;
    overflow: hidden;
    border-radius: 4px;
    display: flex;
    flex-direction: column;
}
.demo2 .no1_2_nav{
    width: 100%;
    height: 60px;
    background: #fff;
    /* padding-top: 15px; */
    margin-bottom: 20px;
    border-radius: 4px 4px 0 0 ;
    display: flex;
    justify-content: space-between;
}
.demo2 .no1_1_navtop{
    height: 60px;
    background:#fff;
    padding-left: 10px;
    padding-top: 20px;
    flex: 1;
} 
.demo2 .navtop_left{
    box-sizing: border-box;
    margin: 0;
    width: 60px;
    height:35px;
    padding: 0 .6em;
    font-size: 1em;
    white-space: nowrap;
    cursor: pointer;
    background: #fff;
    border: 1px solid #ccc;

    outline: none;
    border-radius: 5px;
}
.demo2 .navtop_rigth{
    box-sizing: border-box;
    margin: 0;
    width: 60px;
    height: 35px;
    padding: 0.6em;
    font-size: 1em;
    white-space: nowrap;
    cursor: pointer;
    margin-left:2px;
    border-radius: 5px;
    border: none;
    outline: none;
    background: #f0f0f0;
}
.demo2 .no1_2_content{
    flex: 1;
}
.demo2 .bg{
    background: #3e84e9;
    color: #fff;
}
.demo2 .nav_center{
    flex: 1;
    height: 60px;
    float: left;
    padding-top: 20px;
    border-radius: 0px 0px 4px 4px;
    text-align: center;
}
.demo2 .nav_center{
    display: flex;
    justify-content: space-between;
}
.demo2 .nav_center > h2{
    flex: 1;
}
.demo2 .zs{
    line-height: 35px;  
    text-align: center;
}
.demo2 .zs button{
    border: none;
    outline: none;
    background-color: #fff;
}
.demo2 .zz button{
    border: none;
    outline: none;
    background-color: #fff;
}
.demo2 .zz{
    line-height: 35px;
    float: left;
    text-align: center;
}
.demo2 .rq{
    width: 33%;
    line-height: 35px;
    float: left;
    text-align: center;
}
.demo2 .nav_rigth{
    height: 60px;
    background-color:#fff;
}

.demo2 .ant-btn-primary{
    margin-top:20px;
}
.demo2 .nav_box {
    z-index: 20;
}
.demo2 .no1_2_nav > div{
    flex: 1;
}
.demo2 .no1_2_nav .nav_rigth{
    display: flex;
    justify-content: flex-end;
}
.demo2 thead{
    text-align: center;
}
.demo2 .ant-picker-calendar-full .ant-picker-panel .ant-picker-body th{
    padding: 20px 12px 20px 0px;
}
.demo2 .ass{
    width:50%;
    height: 30px;
    border: 1px solid #ccc;
    margin-left: 25px;
}
.demo2 .acc{
    margin-left: 64px;
}
.demo2 .app{
    margin-left: 50px;
}
.demo2 .acc:hover{
    border: 1px solid red;
}
.demo2 .app:hover{
    border: 1px solid red;
}

.demo2 .azz{
    margin-left: 40px;
}

.demo2 .no2_2_axx{
    width: 100%;
    height: 150px;
    outline: none;
}
.demo2 .no1_2_zzzzz{
    display: inline-block;
    margin-right: 50px;
}
.demo2 .no1_2_guanlian{
    margin-top: 30px;
}
.ant-modal-root .ant-modal-mask{
    background-color: #efefef !important;
}
