.demo1create .title-box{
    display: flex;
    justify-content: space-between;
    height: 40px;
    line-height: 40px;
    font-size: 17px;;
}
.demo1create .top{
    display: flex;
}
.demo1create .top .create-left{
    box-sizing: border-box;
    margin-right: 20px;
}
.demo1create .top .create-right{
    margin-left: 20px;
}
.demo1create .info{
    width: 100%;
    font-size: 12px;
    line-height: 3;
    color: #999;
}
.demo1create .top .ant-space-vertical{
    width: 100%;
}
.demo1create .top .ant-picker{
    width: 100%;
}
.demo1create .top >div{
    flex: 1;
}
.demo1create > div{
    margin-bottom: 15px;
}
.demo1-level li{
    display: inline-block;
    width: 34px;
    height: 34px;
    text-align: center;
    line-height: 34px;
    margin-right: 15px;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
}
