.man7_container{
    height: 60px;
    position: relative;
    z-index: 100;
}
.man7_title{
    float: left;
    /* padding: 0 20px; */
    font-size: 18px;
    line-height: 60px;

}
.man7_input{
    width: 300px;
    height: 34px;
    margin: -18px 0 0 -150px;
    position: absolute;
    left: 50%;
    top: 50%;
}
.man7_dropdown{
    float: right;
    margin-top: 10px;
}
.business_opportunity{
    float: left;
    margin-right: 5px;
}
.product{
    width: 620px;
    padding-top: 10px;
}
.product_content{
    width: 620px;
    height: 222px;
}
.product_header{
    border-bottom: 1px solid #efefef;
}
th{
    width: 80px;
    height: 65px;
    font-weight: 400;
}
.cell{
    line-height: 23px;
    padding-left: 10px;
    padding-right: 10px;
}
.relation_content{
    height: 370px;
    border-bottom: 1px solid #e6e6e6;
}
.relation_title{
    padding: 0 20px;
    font-size: 16px;
    line-height: 50px;
    height: 50px;
    border-bottom: 1px solid #e6e6e6;
}
.relation_input{
    width: 200px;
    height: 34px;
    float: left;
    padding-top: 10px;
}
.relation_table{
    padding-top: 65px;
}
.click{
    color: #c0c4cc;
    cursor: not-allowed;
    background-image: none;
    background-color: #fff;
    border-color: #ebeef5;
}