    margin-bottom: 20px;
}
.demo5-frame .demo5-operate li{
    margin-left: 20px;
}
.demo5-frame .demo5-info{
    text-align: center;
    font-size: 14px;
    background: #eee;
    height: 40px;
    line-height: 40px;
    margin: 0 -24px;
    margin-bottom: 10px;
}
.demo5-frame .cont{
    display: flex;
