.demo8{
    height: 100%;
    display: flex;
    flex-direction: column;
}
.demo8 .demo8-cont{
    flex: 1;
    background-color: #fff;
    border: 1px solid #e6e6e6;
}
.demo8 .demo8-user{
    padding-left: 15px;
    font-size: 16px;
    margin-bottom: 10px;
    line-height: 40px;
}
.demo8 .demo8-user span{
    margin: 0 5px;
}
.demo8-empty img{
    width: 150px;
    height: 120px;
}
.demo8-empty {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.demo8-name .ipt-box{
    width: 100%;
}
.ant-popover-inner-content{
    padding: 12px 16px !important;
}
.ant-popover-buttons{
    padding: 0;
}