
.no4 .ant-menu .create {
  background: #1890ff;
  border-color: #1890ff;
}

.no4 .logo{
  color: rgba(255, 255, 255, 0.65);
  background: #001529;
  margin: 30px 24px 15px 24px;
  
}
.no4 .flex{
  display: flex;
  justify-content: space-between;
  width: 100%;
}
.no4 .flex{
  width: 100%;
  border-radius: 5px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
  background-color: #1890ff;
  height: 30px;
  line-height: 30px;
  color: #fff;
}
.no4 .flex span{
  display: inline-block;
  width: 40px;
  text-align: center;
}

.no4 .ant-btn{
  padding: 0 10px;
}
.no4 .flex p{
  padding: 0 15px;
}

