.man10_container{
    height: 60px;
    position: relative;
    z-index: 100;
}
.man10_title{
    float: left;
    /* padding: 0 20px; */
    font-size: 18px;
    line-height: 60px;

}
.man10_input{
    width: 300px;
    height: 34px;
    margin: -18px 0 0 -150px;
    position: absolute;
    left: 50%;
    top: 50%;
}
.man10_dropdown{
    float: right;
    margin-top: 10px;
}
.fabric{
    float: left;
    margin-right: 5px;
}
.fabric_content{
    padding: 0 20px;
    height: 295px;
}
.fabric_left{
    padding-left: 0;
    padding-right: 30px;
    float: left;
    width: 300px;
    height: 62px;
}
.fabric_right{
    padding-left: 24px;
    padding-right: 0;
    width: 306px;
    height: 62px;
    float: left;
}