.man8_container{
    height: 60px;
    position: relative;
    z-index: 100;
}
.man8_title{
    float: left;
    /* padding: 0 20px; */
    font-size: 18px;
    line-height: 60px;

}
.man8_input{
    width: 300px;
    height: 34px;
    margin: -18px 0 0 -150px;
    position: absolute;
    left: 50%;
    top: 50%;
}
.man8_dropdown{
    float: right;
    margin-top: 10px;
}
.agreement{
    float: left;
    margin-right: 5px;
}
.agreement_content{
    /* padding: 0 10px; */
    padding: 0 0 0 22px;
    height: 413px;
}
.agreement_left{
    padding-left: 0;
    padding-right: 30px;
    float: left;
    width: 310px;
    height: 62px;
}
.agreement_right{
    padding-left: 30px;
    padding-right: 0;
    width: 310px;
    height: 62px;
    float: left;
}
.agreement_header{
    border-bottom: 1px solid #efefef;
    color: #909399;
    font-size: 13px;
    padding-left: 10px;
}
th{
    width: 80px;
    height: 65px;
    font-weight: 400;
}
.cell{
    line-height: 23px;
    padding-left: 10px;
    padding-right: 10px;
}
.as_content{
    padding: 0 20px;
}
.as_header{
    align-items: center;
    padding: 5px 15px;
    display: flex;
}
.as_mark{
    border-left-width: 2px;
    border-left-style: solid;
    height: 10px;
    border-left-color: rgb(70, 205, 207);
}
.as_title{
    font-size: 13px;
    color: #333;
    margin-left: 8px;
    float: left;
}
.as_type{
    font-size: 12px;
    color: #fff;
    background-color: #fd715a;
    padding: 0 8px;
    margin-left: 5px;
    height: 16px;
    line-height: 16px;
    border-radius: 8px;
    transform: scale(.8);

}
