.demo3 {
  height: 100%;
}
.demo3 .demo3-cont{
  height: 100%;
  position: relative;
}
.demo3 .ant-table-wrapper,
.demo3 .ant-spin-nested-loading,
.demo3 .ant-spin-container,
.demo3 .ant-table ant-table-empty,
.demo3 .ant-table-empty,
.demo3 .ant-table-container,
.demo3 .ant-table-body table {
  height: 100%;
}
