.demo3 .ant-table-container {
  display: flex;
  flex-direction: column;
}
.demo3 .ant-table-body {
  flex: 1;
}

.demo3 .demo3-title {
  font-size: 16px;
  line-height: 40px;
  padding-left: 15px;
  margin-bottom: 10px;
}
.demo3 .demo7-count-box {
  height: calc(100% - 50px);
}
.demo3 .demo7-count {
  display: flex;
  flex-direction: column;
}
.demo3 .overview {
  margin-bottom: 10px;
}
.demo3 .demo7-count-footer {
  flex: 1;
}
.demo3 .demo7-count-footer {
  display: flex;
  flex-direction: column;
}
.demo3 .demo7-table {
  flex: 1;
} 
.demo3 .ant-select{
  position: absolute;
  right: 0;
  top: 0;
}
 