.demo4{
    position: relative;
    min-height: 100%;
    width: 100%;
}
.demo4 .demo4-empty{
    top: 50%;
    width: 100%;
    text-align: center;
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
}
.demo4-empty img{
    width: 150px;
}
.demo4 .demo4-title{
    height: 40px;
    line-height: 40px;
    font-size: 18px;
    margin-bottom: 10px;
    padding-left: 20px;
}
.demo4{
    display: flex;
    flex-direction: column;
}
.demo4 .demo4-cont{
   flex: 1;
   background-color: #fff;
}
