.demo5{
    min-height: 100%;
}
.demo5 .demo5-title{
    height: 40px;
    line-height: 40px;
    font-size: 16px;
    margin-bottom: 5px;
    padding-left: 15px;
}
.demo5 .demo5-item{
    border: 1px solid #e6e6e6;
    min-height: 100%;
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
    background-color: #fff;
}
.demo5 .demo5-item li > div{
    display: flex;
    justify-content: space-between;
    height: 50px;
    line-height: 50px;
    padding: 0 10px;
    border-bottom: 1px solid #e6e6e6;
}
.demo5 .demo5-userImg{
    width: 30px;
    height: 30px;
    line-height: 30px;
    background-color: #1890ff;
    overflow: hidden;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
    font-size: 12px;
    color: #fff;
    text-align: center;
    margin-top: 10px;
}
.demo5 .demo5-left{
    font-size: 14px;
    flex: 1;
    color: #8f8f8f;
}
.demo5 .demo5-right{
    display: flex;
    
}
.demo5 .demo5-right{
    font-size: 12px;
    color: #666;
}
.demo5 .demo5-right span{
    display: inline-block;
    margin-left: 5px;
}
.demo5 .demo5-userImg{
    margin-left: 15px;
}

