.demo1 .title{
    height: 40px;
    line-height: 40px;
    position: relative;
    z-index: 100;
    padding: 0 20px;
    font-size: 18px;
}
.demo1 .demo1-position{
    overflow-y: hidden;
    overflow-x: auto;
    white-space: nowrap;
    padding: 10px 0;
    position: relative;
    height: calc(100% - 40px);
}
.demo1 .demo1-cont{
    overflow-y: hidden;
    overflow-x: auto;
    position: absolute;
    top: 0;
    bottom: 0;
    width: 100%;
}
.demo1 .item{
    width: 280px;
    display: inline-block;
    margin: 0 7px;
    overflow: hidden;
    box-sizing: border-box;
    padding: 10px;
    background-color: #fff;
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
}
.demo1-cont .info-title{
    margin-bottom: 5px;
}
.demo1-cont .info-title >p {
    font-size: 14px;
    display: inline-block;
    margin-right: 10px;
}
.demo1-line{
    width: 100%;
    height: 6px;
    margin-bottom: 10px;
    background-color: #efefef;
    border-radius: 3px;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    -ms-border-radius: 3px;
    -o-border-radius: 3px;
}
.demo1 .demo1-cont-list{
    min-height: 20px;
    margin-bottom: 10px;
}
.demo1 .newcreate{
    font-size: 12px;
    color: #999;
    display: flex;
    line-height: 1;
}
.demo1 .anticon{
    color: #2486e4;
    margin-right: 5px;
}
.demo1{
    height: 100%;
}
.demo1-cont::-webkit-scrollbar-track{    /*设置滚动条轨迹 border-radius等*/
    background-color: #c6cad6;
}
.demo1-cont::-webkit-scrollbar{
    /*滚动条整体样式，可设置width,height*/width: 100%;
    height: 6px;
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
}
.demo1-cont::-webkit-scrollbar-thumb{
    /*设置滚动条内小方块background-color*/
    background-color: #acadb1;
    border-radius: 3px;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    -ms-border-radius: 3px;
    -o-border-radius: 3px;
}
.demo1 .ant-modal-confirm-warning .ant-modal-confirm-body > .anticon, .ant-modal-confirm-confirm .ant-modal-confirm-body > .anticon{
    display: none;
}
.ant-modal-confirm-body-wrapper .ant-modal-confirm-body > .anticon + .ant-modal-confirm-title + .ant-modal-confirm-content{
    margin-left: 0;
}
.ant-modal-root .ant-modal-mask{
    background-color: #f5f6f9;
}

.ant-modal-root .ant-modal-confirm-confirm{
    width: 700px !important;
}