.demo7{
    height: 100%;
}
.demo7-top-box{
    /* background-color: #fff;
    padding: 0 30px; */
    height: 100%;
    font-size: 14px;
    display: flex;
    flex-direction: column;
}
.demo7 .ant-tabs{
    flex: 1;
}
.demo7 .ant-tabs-content{
    height: 100%;
}
.demo7-top-box .demo7-user-box{
    display: flex;
    justify-content: space-between;
    line-height: 40px;
    font-size: 16px;
    background-color: #fff;
    padding: 10px 30px;
}
.demo7 .ant-tabs-nav{
    background-color: #fff;
    padding: 0 50px;
    margin-bottom: 15px !important;
}
.demo7 .ant-tabs-nav{
    width: 100%;
}
.demo7 .ant-tabs-nav{
    border-bottom: none;
}
.demo7 .ant-tabs-top > .ant-tabs-nav{
    margin: 0;
}
.demo7-top-box .demo7-user-box .user-tags > div{
    display: inline-block;
    margin-left: 15px;
    color: #666;
}
.demo7-top-box .demo7-list-tag li{
    display: inline-block;
    margin: 0 20px;
    line-height: 30px;
}
.demo7-list-tag .act{
    color: #3e84e9;
    border-bottom: solid 2px #3e84e9;
    box-sizing: border-box;
}
.demo7 .demo7-cont{
    overflow-y: hidden;
    overflow-x: auto;
    position: absolute;
    top: 0;
    bottom: 0;
    width: 100%;
}
.demo7 .item{
    width: 280px;
    display: inline-block;
    vertical-align: top;
    margin: 0 7px;
    overflow: hidden;
    box-sizing: border-box;
    padding: 10px;
    background-color: #fff;
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
}
.demo7-cont .info-title{
    margin-bottom: 5px;
}
.demo7-cont .info-title >p {
    font-size: 14px;
    display: inline-block;
    margin-right: 10px;
}
.demo7-line{
    width: 100%;
    height: 6px;
    margin-bottom: 10px;
    background-color: #efefef;
    border-radius: 3px;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    -ms-border-radius: 3px;
    -o-border-radius: 3px;
}
.demo7 .demo7-cont-list{
    min-height: 20px;
    margin-bottom: 10px;
}
.demo7 .newcreate{
    font-size: 12px;
    color: #999;
    display: flex;
    line-height: 1;
}
/* .demo7 .anticon{
    color: #2486e4;
    margin-right: 5px;
} */
.demo7 .last-item{
    text-align: center;
    color: #999;
    display: inline-block;
    vertical-align: top;
}
.demo7 .last-item .info-title{
    margin: 0;
}
.demo7 .demo7-position {
    overflow-y: hidden;
    overflow-x: auto;
    white-space: nowrap;
    padding: 10px 0;
    position: relative;
    height: 100%;
}
.demo7-cont::-webkit-scrollbar-track{    /*设置滚动条轨迹 border-radius等*/
    background-color: #c6cad6;
}
.demo7-cont::-webkit-scrollbar{
    /*滚动条整体样式，可设置width,height*/width: 100%;
    height: 6px;
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
}
.demo7-cont::-webkit-scrollbar-thumb{
    /*设置滚动条内小方块background-color*/
    background-color: #acadb1;
    border-radius: 3px;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    -ms-border-radius: 3px;
    -o-border-radius: 3px;
}
.demo7 .ant-tabs-content-holder{
    flex: 1
}
.demo7 .ant-table-wrapper, .demo7 .ant-spin-nested-loading, .demo7 .ant-table, .demo7 .ant-spin-container, .demo7 .ant-table-body,  .demo7 .ant-table-body >table{
    height: 100%;
}
.demo7 .ant-table-container{
    height: 100%;
    display: flex;
    flex-direction: column;
}
.demo7 .ant-table-body{
    flex: 1;
}
.demo7 .ant-table-thead > tr > th{
    text-align: center;
}
.demo7 .demo7-empty {
    min-height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #fff;
}
.demo7 .demo7-empty img{
    width: 150px;
}
.demo7-user span{
    margin: 0 5px;
    color: #666;
}
.ant-popover .setlist{
    margin-bottom: 0;
    padding: 15px 0;
    line-height: 2.2;
    font-size: 14px;
    margin: -10px -16px;
}
.ant-popover .setlist li{
    padding: 0 30px;
}
.ant-popover .setlist li:hover{
    background: #f7f8fa;
    color: #3e84e9;
}
.demo7-user-list-list li{
    display: inline-block;
    vertical-align: middle;
    overflow: hidden;
}

.demo7-user-list .userImg{
    width: 25px;
    height: 25px;
    margin-right: 5px;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
}
.demo7-user-list .userImg img{
    width: 100%;
    height: 100%;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
}
.demo7-user-list .userImg span{
    display: inline-block;
    width: 100%;
    height: 100%;
    line-height: 25px;
    font-size: 10px;
    text-align: center;
    background-color: #3e84e9;
    color: #fff;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
}
.ant-drawer-body .add{
    margin-bottom: 15px;
    font-size: 12px;
    color: #3e84e9;
}
.ant-drawer-body .demo7-user-list li{
    display: flex;
    margin: 8px 0;
}
.ant-drawer-body .demo7-user-list .realname{
    flex: 1;
    margin-left: 10px;
}
.menuBox .ant-menu-sub.ant-menu-inline{
    background: transparent;
}
.demo7 .btnsm button{
    margin: 0 5px;
}
.demo7 .btnsm button:nth-child(2){
    color: #999;
}
.demo7 .btnsm {
    text-align: right;
}
.demo7-ins-item input{
    margin-bottom: 14px;
} 
.demo7 .ant-picker{
    opacity: 0;width: 20px;height: 20px;vertical-align: top;
}
.demo7-time{
    position: relative;display: inline-block;
}
.demo7-time .icons{
    position: absolute;left: 0;top: 0;width: 20px;height: 22px;display: inline-block;line-height: 22px;padding-top: 2px;
}
.demo7-iconse{
    text-align: right;margin-bottom: 15px;
}
.demo7-icon-user{
    display: inline-block;width: 24px;height: 22px;text-align: center;
}
