.demo7-count{
    height: 100%;
}
.demo7-count .overview-cont{
    display: flex;
    margin-bottom: 10px;
    padding: 0 100px;
}
.demo7-count .overview-cont > div{
    flex: 1;
    text-align: center;
}
.demo7-count .demo7-count-mid{
    display: flex;
}
.demo7-count .overview-cont-left{
    display: flex;
}
.demo7-count .overview-cont-left > div{
    margin: 10px;
}
.demo7-count .overview{
    background-color: #fff;
}
.demo7-count .demo7-count-mid{
    display: flex;
    margin-bottom: 10px;
}
.demo7-count .demo7-count-mid > div{
    flex: 1;
    background-color: #fff;
}
.demo7-count .demo7-count-mid > div:nth-child(1){
    margin-right: 10px;
}
.demo7-count .demo7-count-wraptitle{
    line-height: 1;
    font-size: 12px;
    color: #333;
    border-left: solid 2px rgb(70, 205, 207);
    margin: 10px 20px;
    padding-left: 10px;
}
.demo7-count .demo7-count-footer{
    background-color: #fff;
}
.demo7-count .demo7-table{
    padding: 10px;
}
.demo7-count-box{
    position: relative;
    overflow-y: auto;
    height: 100%;
}
.demo7-count{
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
}
.demo7-count-box::-webkit-scrollbar-track{    /*设置滚动条轨迹 border-radius等*/
    background-color: #c6cad6;
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
}
.demo7-count-box::-webkit-scrollbar{
    /*滚动条整体样式，可设置width,height*/
    width: 6px;
    height: 6px;
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
}
.demo7-count-box::-webkit-scrollbar-thumb{
    /*设置滚动条内小方块background-color*/
    background-color: #acadb1;
    border-radius: 3px;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    -ms-border-radius: 3px;
    -o-border-radius: 3px;
}
.demo7-count .demo7-count-userImg{
    width: 40px;
    height: 40px;
    margin-right: 5px;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
    margin-left: auto;
    margin-right: auto;
}
.demo7-count .demo7-count-userImg img{
    width: 100%;
    height: 100%;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
}
.demo7-count .demo7-count-userImg span{
    display: inline-block;
    width: 100%;
    height: 100%;
    line-height: 40px;
    font-size: 14px;
    text-align: center;
    background-color: #3e84e9;
    color: #fff;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
}
.demo7-count .demo7-count-userBox{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

