.header{
    display: flex;
    justify-content: space-between;
    padding: 15px 30px;
    height: 40px;
    line-height: 40px;
    box-sizing: content-box;
    box-shadow: 0 1px 2px #dbdbdb;
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    background-color: #fff;
    z-index: 99;
}
.header .logo{
    width: 150px;
    margin-right: 20px;                                                                                                                                                                                                                                                 
}
img{
    width: 100%;
    height: 100%;
}
.left{
    display: flex;
}
.nav ul{
    display: flex;
}
.nav li{
    padding: 0 20px;
    margin-right: 30px;
}
.right{
    display: flex;
}
.opened{
    display: inline-block;
    vertical-align: center;
    height: 16px;
    font-size: 12px;
    line-height: 1;
    background: linear-gradient(90deg,#d9ac7c,#c28e5c);
    color: #fff;
    padding: 0 15px;
    border-radius: 8px;
    -webkit-border-radius: 8px;
    -moz-border-radius: 8px;
    -ms-border-radius: 8px;
    -o-border-radius: 8px;
    margin-right: 30px;
    line-height: 16px;
}
.demos{
    display: flex;
    align-items: center;
}
.nav .ant-menu{
    font-size: 16px;
    height: 40px;
    line-height: 40px;
}
.demo .name{
    height: 100%;
    width: 40px;
    line-height: 40px;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
    background-color: #2486e4;
    text-align: center;
    font-size: 12px;
    color: #fff;
    margin-right: 8px;
}
.nav .ant-menu-horizontal:not(.ant-menu-dark) > .ant-menu-item, .ant-menu-horizontal:not(.ant-menu-dark) > .ant-menu-submenu{
    margin: 0 30px;
    font-size: 14px;
}
.demos .arrow{
    display: inline-block;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 6px 6px 0 6px;
    border-color: #ccc transparent transparent transparent;
}
.demos:hover .arrow{
    border-color: #2486e4 transparent transparent transparent;
}
.nav a{
    color: inherit;
}
.header .ant-menu-horizontal{
    border-bottom: none;
}
.nav .ant-menu-horizontal:not(.ant-menu-dark) > .ant-menu-item:hover, .ant-menu-horizontal:not(.ant-menu-dark) > .ant-menu-submenu:hover, .ant-menu-horizontal:not(.ant-menu-dark) > .ant-menu-item-active, .ant-menu-horizontal:not(.ant-menu-dark) > .ant-menu-submenu-active, .ant-menu-horizontal:not(.ant-menu-dark) > .ant-menu-item-open, .ant-menu-horizontal:not(.ant-menu-dark) > .ant-menu-submenu-open, .ant-menu-horizontal:not(.ant-menu-dark) > .ant-menu-item-selected, .ant-menu-horizontal:not(.ant-menu-dark) > .ant-menu-submenu-selected{
    border-color: transparent;
}
.nav .ant-menu-item:active, .ant-menu-submenu-title:active{
    transition: none;
    -webkit-transition: none;
    -moz-transition: none;
    -ms-transition: none;
    -o-transition: none;
}
.cont2style{
    text-align: center;
    /* padding: 10px; */
}
.cont2style .title{
    font-size: 16px;
    line-height: 1.5;
}
.cont2style .text{
    font-size: 12px;
    line-height: 1.2;
    margin: 6px 0;
}
.cont2style .btn{
    text-align: center;
}
.cont2style .btn span{
    display: inline-block;
    font-size: 12px;
    line-height: 1.5;
    background: linear-gradient(90deg,#d9ac7c,#c28e5c);
    color: #fff;
    padding: 0 10px;
    border-radius: 10px;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    -ms-border-radius: 10px;
    -o-border-radius: 10px;
}
.contstyle{
    border-radius: 8px;
    -webkit-border-radius: 8px;
    -moz-border-radius: 8px;
    -ms-border-radius: 8px;
    -o-border-radius: 8px;
    /* padding: 10px 20px; */
}
.contstyle p{
    font-size: 14px;
    line-height: 2;
    color: #666;
}
.contstyle .btn{
    border-radius: 6px;
    border-color: #009df0;
    background-color: #009df0;
    color: #fff;
    width: 100%;
    font-size: 14px;
    line-height: 2;
    padding: 0 10px;
    margin: 8px 0;
    -webkit-border-radius: 6px;
    -moz-border-radius: 6px;
    -ms-border-radius: 6px;
    -o-border-radius: 6px;
}
.contstyle a{
    color: inherit;
}