.lxkh{
    width: 830px;
    height: 498px;
    background-color: #fff;
    border: 1px solid #e6e6e6;
    border-radius: 2px;
}
.lxkh_title{
    padding: 0 20px;
    font-size: 16px;
    line-height: 60px;
    height: 60px;
}
.option_bar{
    width: 810px;
    height: 44px;
    padding: 5px 20px;
}
.option_data{
    height: 34px;
}
.ant-modal-header{
    padding: 16px 24px 0 24px;
    border-bottom: none;
}
.ant-modal-body {
    padding: 10px 27px;
}
.ant-modal-footer{
    border-top: none;
}
.ant-table-container{
    padding-left: 0;
}
.ant-pagination {
    display: block;
    float: right;
    margin-right: 50px;
    margin-top: 10px;
}
.ant-table-thead > tr > th {
    width: 105px;
    height: 48px;
    border: 1px solid #f0f0f0;
}
.ant-table-thead>tr>th:nth-child(8){
 width: 144px;
 height: 48px;
}
.ant-table-thead>tr>th:nth-child(16){
    width: 144px;
    height: 48px;
}