.man2_header{
    height: 60px;
    font-size: 18px;
    padding: 0 20px;
    line-height: 60px;
}
.man2_body{
    position: relative;
    height: 100%;
}
.man2_body_side{
    width: 200px;
    height: 498px;
    font-size: 14px;
    background-color: #fff;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    z-index: 1;
    border: 1px solid #e6e6e6;
    border-radius: 2px;
    overflow-y: auto;
    
}
.side_select{
    position: relative;
    height: 50px;
    line-height: 50px;
    padding: 0 20px;
    font-size: 13px;
    cursor: pointer;
    color: #333;
    background-color: #fff;
}
.select_default{
    color: #333;
    border-right: 2px solid transparent;
}
.side_select a{
    color: #000;
}
.side_select:hover{
    background-color: #ecf5ff;
    border-right: 2px solid #46cdcf;

}
.man2_body_content{
    margin-left: 210px;
    width: 813px;
    height: 498px;
    overflow: hidden;
    background-color: #fff;
    border: 1px solid #e6e6e6;
    border-radius: 2px;
}
.man2_body_side .act{
    color:#409eff;
    background : #ecf5ff ;
    border-right: 2px solid #46cdcf;
}