.man9_container{
    height: 60px;
    position: relative;
    z-index: 100;
}
.man9_title{
    float: left;
    /* padding: 0 20px; */
    font-size: 18px;
    line-height: 60px;

}
.man9_input{
    width: 300px;
    height: 34px;
    margin: -18px 0 0 -150px;
    position: absolute;
    left: 50%;
    top: 50%;
}
.man9_dropdown{
    float: right;
    margin-top: 10px;
}
.money{
    float: left;
    margin-right: 5px;
}
.money_content{
    padding: 0 20px;
    height: 295px;
}
.money_section{
    padding: 0 12px;
}
.money_left{
    padding-left: 0;
    padding-right: 30px;
    float: left;
    width: 300px;
    height: 62px;
}
.money_right{
    padding-left: 24px;
    padding-right: 0;
    float: left;
    width: 306px;
    height: 62px;
}
.ms_content{
    padding-left: 7px;
}
.ms_header{
    align-items: center;
    padding: 5px 5px;
    display: flex;
}
.ms_mark{
    border-left-width: 2px;
    border-left-style: solid;
    height: 10px;
    border-left-color: rgb(70, 205, 207);
}
.ms_title{
    font-size: 13px;
    color: #333;
    margin-left: 8px;
    float: left;
}
.ms_type{
    font-size: 12px;
    color: #fff;
    background-color: #fd715a;
    padding: 0 8px;
    margin-left: 5px;
    height: 16px;
    line-height: 16px;
    border-radius: 8px;
    transform: scale(.8);

}