.man4_container{
    height: 60px;
    position: relative;
    z-index: 100;
}
.man4_title{
    float: left;
    /* padding: 0 20px; */
    font-size: 18px;
    line-height: 60px;

}
.man4_input{
    width: 300px;
    height: 34px;
    margin: -18px 0 0 -150px;
    position: absolute;
    left: 50%;
    top: 50%;
}
.man4_dropdown{
    float: right;
    margin-top: 10px;
}
.man4_content{
    height: 410px;
    background-color:#fff;
    border: 1px solid #e6e6e6;
    border-radius: 2px;
    
}
.dd_left{
    float: left;
    margin-right: 5px;
}
.box_left{
    padding-left: 0;
    padding-right: 25px;
}
.box_right{
    padding-left: 25px;
    padding-right: 0;
}
.box_loaction{
    width: 700px;
    padding-top: 10px;
}
.loaction_content{
    width: 611px;
    height: 300px;
}
.loaction_left{
    width: 276px;
    margin-right: 50px;
    float: left;
}
.loaction_right{
    margin-left: 8px;
    width: 276px;
    float: left;

}
.loaction_title{
    font-size: 12px;
    color: #aaa;
    padding-left: 10px;
    padding-bottom: 10px;
}