.transfer{
    min-width: 600px;
    padding: 10px 20px;
}
.transfer .title-box{
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid #ccc;
    line-height: 50px;
    font-size: 14px;
    margin-bottom: 10px;
}
.transfer .title-box .icon{
    color: #ccc;
}
.transfer .tran-cont{
    display: flex;
}
.transfer .tran-cont{
    margin: 0 -10px;
}
.transfer .tran-cont>div{
    flex: 1;
    margin: 0 10px;
}
.transfer .tran-cont .hd-box{
    text-align: center;
    line-height: 30px;
    margin-bottom: 20px;
    color: #3e84e9;
}
.transfer .tran-cont .hd-box .select{
    color: #666;
}
.transfer .tran-right .hd-box{
    display: flex;
    justify-content: space-between;
}
.transfer .tran-member-box{
    border: solid 1px #ccc;
    height: 300px;
    padding: 10px 20px;
    margin-bottom: 20px;
}
.transfer .tran-footer{
    display: flex;
    justify-content: flex-end;
}
.transfer .tran-footer > button{
    margin-left: 10px;
}
.transfer .tran-left .search-box{
    width: 100%;
    position: relative;
    height: 25px;
    line-height: 25px;
    border: solid 1px #ccc;
    border-radius: 13px;
    -webkit-border-radius: 13px;
    -moz-border-radius: 13px;
    -ms-border-radius: 13px;
    -o-border-radius: 13px;
    box-sizing: border-box;
    padding: 0 30px;
    margin-bottom: 10px;
}
.transfer .tran-left .search-box input{
    width: 100%;
    font-size: 12px;
}
.transfer .tran-left .search-box span{
    position: absolute;
    width: 30px;
    /* height: 25px; */
    font-size: 14px;
    text-align: center;
    /* line-height: 25px; */
    top: 3px;
    left: 0;
}
.transfer .tran-member{
    height: 248px;
    overflow: auto;
}
.transfer .tran-list{
    display: flex;
    height: 25px;
    line-height: 25px;
    margin-bottom: 10px;
}
.transfer .tran-list .checkbox{
    display: inline-block;
    vertical-align: middle;
    width: 14px;
    height: 14px;
    margin-bottom: 10px;
}
.transfer .tran-list .check{
    display: inline-block;
    height: 15px;
    width: 15px;
    border: solid 1px #ccc;
    position: relative;
    border-radius: 2px;
    -webkit-border-radius: 2px;
    -moz-border-radius: 2px;
    -ms-border-radius: 2px;
    -o-border-radius: 2px;
}
.transfer .tran-list .check2{
    background-color: #3e84e9;
    border-color: #3e84e9;
}
.transfer .tran-list .check2 i{
    display: inline-block;
    border: 1px solid #fff;
    border-left: 0;
    border-top: 0;
    height: 7px;
    width: 4px;
    left: 5px;
    position: absolute;
    top: 2px;
    transform: rotate(45deg);
    -webkit-transform: rotate(45deg);
    -moz-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    -o-transform: rotate(45deg);
}
.transfer .tran-list .username{
    display: inline-block;
    vertical-align: middle;
    flex: 1;
}
.transfer .tran-list .userImg{
    overflow: hidden;
    width: 25px;
    height: 25px;
    margin: 0 10px;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
}
.transfer .tran-list .userImg img{
    width: 100%;
    height: 100%;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
}
.transfer .tran-list .userImg span{
    display: inline-block;
    font-size: 10px;
    text-align: center;
    background-color: #3e84e9;
    color: #fff;
}
.transfer .tran-member::-webkit-scrollbar-track{
    /*设置滚动条轨迹border-radius等*/background-color: #c6cad6;
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
}
.transfer .tran-member::-webkit-scrollbar{
    /*滚动条整体样式，可设置width,height*/width: 5px;
    height: 100%;
}
.transfer .tran-member::-webkit-scrollbar-thumb{
    /*设置滚动条内小方块background-color*/
    background-color: #acadb1;
    border-radius: 3px;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    -ms-border-radius: 3px;
    -o-border-radius: 3px;
}
.transfer .ant-btn-primary{
    position: absolute;
    bottom: -32px;
    right: 0;
    z-index: 9999;
}