.login {
  display: flex;
  height: 100vh;
  position: relative;
}
.login .img {
  width: 68%;
  height: 100%;
}
.login .pic {
  position: absolute;
  left: 60px;
  top: 50px;
  width: 180px;
  height: 48px;
  z-index: 200;
}
.login img {
  width: 100%;
  height: 100%;
}
.login-box {
  flex: 1;
}
.login-box {
  padding-top: 6%;
  padding-bottom: 1%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
.login_in,
.info {
  width: 80%;
}
.login-box .info {
  font-size: 12px;
  color: #ccc;
  text-align: center;
  line-height: 2;
}
.login-box .hd {
  font-size: 26px;
  margin-bottom: 40px;
  text-align: center;
}
.login .ant-input-affix-wrapper {
  border: none;
}
.login .ant-form-item-control-input {
  border-bottom: 1px solid #ccc;
}
.login .ant-form-item-control-input:last-child {
  border: none;
}
.login .ant-btn-primary {
  width: 100%;
}
.login .ant-btn {
  height: 48px;
  line-height: 48px;
  padding: 0;
}
.login .ant-row {
  border-bottom: 1px solid #ccc;
}
.login .ant-row:last-child {
  border-bottom: none;
}
.login .ant-input-prefix,
.ant-input-suffix {
  display: none;
}
.login-box
  .ant-input-affix-wrapper:not(.ant-input-affix-wrapper-disabled):hover {
  border: none;
  border-right-width: 0 !important;
}
.login-box .ant-input-affix-wrapper:focus,
.ant-input-affix-wrapper-focused {
  box-shadow: none !important;
  outline: none !important;
}
