.man6_container{
    height: 60px;
    position: relative;
    z-index: 100;
}
.man6_title{
    float: left;
    /* padding: 0 20px; */
    font-size: 18px;
    line-height: 60px;

}
.man6_input{
    width: 300px;
    height: 34px;
    margin: -18px 0 0 -150px;
    position: absolute;
    left: 50%;
    top: 50%;
}
.man6_dropdown{
    float: right;
    margin-top: 10px;
}
.man6_content{
    height: 410px;
    background-color:#fff;
    border: 1px solid #e6e6e6;
    border-radius: 2px;
    
}
.man6content_title{
   font-size: 13px;
   height: 50px;
   padding: 5px 20px 5px 0px;
    
}