.man3_container{
    height: 60px;
    position: relative;
    z-index: 100;
}
.man3_title{
    float: left;
    padding: 0 20px;
    font-size: 18px;
    line-height: 60px;
}
.man3_input{
    width: 300px;
    height: 34px;
    margin: -18px 0 0 -150px;
    position: absolute;
    left: 50%;
    top: 50%;
}
.man3_dropdown{
    float: right;
    margin-top: 10px;
}
.dropdown_left{
    float: left;
    margin-right: 5px;
}
.section_header{
    align-items: center;
    padding: 5px 18px;
    display: flex;
}
.section_mark{
    border-left-width: 2px;
    border-left-style: solid;
    height: 10px;
    border-left-color: rgb(70, 205, 207);
}
.section_title{
    font-size: 13px;
    color: #333;
    margin-left: 8px;
}
.section_content{
    padding: 0 0 0 22px;
}
.content_box{
    display: flex;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
}
.box_left{
    padding-left: 0;
    padding-right: 25px;
}
.box_right{
    padding-left: 25px;
    padding-right: 0;
}
.man3_content{
    height: 410px;
    background-color:#fff;
    border: 1px solid #e6e6e6;
    border-radius: 2px;
    
}
.ant-layout-content {
margin: 0;
}
.content_title{
    font-size: 13px;
    height: 50px;
    padding: 5px 20px;
}
.ant-table-cell-fix-right {
    display: block;
}

.ant-table-container table > thead > tr:first-child th:last-child {
    width: 90px;
    height: 56px;
}
