.demo6{
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #fff;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    z-index: 999;
}
.demo6 .demo6-cont{
    width: 700px;
    height: 700px;
    padding: 30px;
    box-shadow: 0 2px 12px 0 rgba(0,0,0,.2);
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
}
.demo6-title{
    display: flex;
    justify-content: space-between;
    font-size: 18px;
    line-height: 2;
    margin-bottom: 20px;
}
.demo6-name .hd{
    border-left: 2px solid #f56c6c;
    padding-left: 10px;
    line-height: 1;
    font-size: 14px;
    margin-bottom: 10px;
}
.demo6-mid .title{
    font-size: 14px;
    margin-bottom: 5px;
}
.demo6-mid > div{
    margin-bottom: 20px;
}
/* .demo6-name .ant-input{
    width: 40%;
} */
.demo6-see .ant-select{
    width: 40% !important;
}
.demo6-cont{
    display: flex;
    flex-direction: column;
}
.demo6-cont .demo6-mid{
    flex: 1;
}
.demo6-footer{
    display: flex;
    justify-content: flex-end;
}
.demo6-footer button{
    margin-left: 15px;
}
.demo6-name .color{
    margin-top: 10px;
}
.demo6-name .color span,.demo6-name .ipt-box span{
    display: inline-block;
    width: 15px;
    height: 15px;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
    margin-right: 5px;
}
.demo6-name input{
    padding: 4px 11px 4px 30px;
}
.demo6-name .ipt-box{
    width: 40%;
    position: relative;
}
.demo6-name .ipt-box span{
    position: absolute;
    left: 8px;
    top: 8px;
}
.demo6-list .ant-btn > .anticon + span, .ant-btn > span + .anticon{
    margin: 0;
}
.member-add .ant-btn{
    padding: 0;
    width: 25px;
    height: 25px;
    line-height: 25px;
    text-align: center;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
}
.demo6-list li{
    display: inline-block;
    vertical-align: middle;
    overflow: hidden;
}

.demo6 .userImg{
    width: 25px;
    height: 25px;
    margin-right: 5px;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
}
.demo6 .userImg img{
    width: 100%;
    height: 100%;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
}
.demo6 .userImg span{
    display: inline-block;
    width: 100%;
    height: 100%;
    line-height: 25px;
    font-size: 10px;
    text-align: center;
    background-color: #3e84e9;
    color: #fff;
}
.ant-popover-content .ant-popover-message > .anticon{
    display: none;
}

.ant-popover-content .ant-popover-message-title{
    padding: 0;
}

.ant-popover-buttons{
    padding: 0 22px;
    padding-bottom: 20px;
}
.ant-popover-buttons button{
    height: 32px;
    padding: 4px 15px;
}
/* .ant-popover-buttons .ant-btn-primary{
    display: none;
} */
.ant-select-tree-switcher{
    width: 0;
}