.createtantext1-1::-webkit-scrollbar {/*滚动条整体样式*/
    width: 5px;     /*高宽分别对应横竖滚动条的尺寸*/
    height: 1px;
}
.createtantext1-1::-webkit-scrollbar-thumb {/*滚动条里面小方块*/
    border-radius: 5px;
    box-shadow: inset 0 0 5px#999;
    background: #C7D1DA;
}
.createtantext1-1::-webkit-scrollbar-track {/*滚动条里面轨道*/
    box-shadow: inset 0 0 5px #999;
    border-radius: 5px;
    background: #EDEDED;
}