.man5_container{
    height: 60px;
    position: relative;
    z-index: 100;
}
.man5_title{
    float: left;
    /* padding: 0 20px; */
    font-size: 18px;
    line-height: 60px;

}
.man5_input{
    width: 300px;
    height: 34px;
    margin: -18px 0 0 -150px;
    position: absolute;
    left: 50%;
    top: 50%;
}
.man5_dropdown{
    float: right;
    margin-top: 10px;
}
.man5_content{
    height: 410px;
    background-color:#fff;
    border: 1px solid #e6e6e6;
    border-radius: 2px;
    
}
.new_contact{
    float: left;
    margin-right: 5px;
}
.ant-table-container table > thead > tr:first-child th:last-child {
    height: 78px;
}
