*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.main{
    height: 100%;
    /* height: 569px; */
    overflow-x: scroll;
    overflow-y: scroll;
}
.main_container{
    height: 100%;
}
.flexbox{
    width: 100%;
    text-align: left;
    margin-bottom: 20px;
    position: relative;
    display: -webkit-flex;
    align-items: center;
}
.photo img{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    margin-right: 16px;
    margin-left: 0;
}
.photo_right{
    width: 565px;
    height: 34px;
    display: inline-block;
    margin-top: 6px;
}
.photo_name{
    font-size: 16px;
    color: #333;
    float: left;
}
.photo_line{
    width: 1px;
    height: 12px;
    background-color: #aaa;
    margin: 8px 14px;
    float: left;
}
.photo_btn{
    float: left;
}
.search_btn{
    margin-left: auto;
}
.data_wrap{
    /* width: 1215px; */
    height: 700px;
    min-width: 800px;
    flex-wrap: wrap;
    display: flex;
    margin-bottom: 20px;
}
.data_wrap .wrap_flex{
    width: 492px;
    height: 370px;
    flex: 0 0 50%;
    float: left;
    padding-right: 10px;
    padding-bottom: 20px;
    background-color: #ccc;
}
.card{
    height: 350px;
    position: relative;
    border: 1px solid #e6e6e6;
    border-radius: 2px;
    padding: 20px;
    background-color: #fff;
}
.mark_header{
    margin-bottom: 20px;
    font-size: 13px;
    line-height: 15px;
    display: inline-flex;
    margin-top: 5px;
}
.mark_header img{
    display: block;
    width: 15px;
    margin-right: 5px;
}
.card_box{
    flex-wrap: wrap;
}
.box_data{
    height: 50px;
    width: 200px;
    float: left;
    margin-bottom: 10px;
    background-color: #f2f2f5;
    border: none;
}
.card_img{
    display: block;
    width: 23px;
    height: 23px;
    margin-right: 10px;
    /* margin-left: 10px; */
    float: left;
    margin-top: 8px;
}
.jianbao_title{
    color: #333;
    font-size: 12px;
    margin-right: 10px;
    line-height: 43px;
    float: left;
}
.jianbao_value{
    color: #333;
    text-align: right;
    font-size: 13px;
    margin-top: 10px;
    margin-right: 10px;
}

.mark_left{
    padding: 10px 25px 25px;
    float: left;
}
.name{
    font-size: 12px;
    color: #666;
}
.value{
    margin-top: 10px;
    font-size: 18px;
    color: #333;
}
.mark_middle{
    padding: 10px 25px 25px;
    float: left;
}
.mark_right{
    width: 81px;
    height: 54px;
    float: right;
}
.mark_border{
    width: 10px;
    height: 10px;
    margin-right: 10px;
    margin-top: 6px;
    float: left;
}
#bing{
    width: 200px;
    height: 200px;
    float: right;
}
#zhu_o{
    width: 430px;
    height: 282px;
}
#yj{
    margin-left: 70px;
    margin-top: 37px;
}
.yjname{
    font-size: 12px;
    color: #666;
}
.yjvalue{
    margin-top: 15px;
    font-size: 14px;
    color: #333;
}
.condition_left{
    padding-left: 0;
    padding-right: 30px;
    float: left;
}
.condition_right{
    width: 295px;
    padding-left: 30px;
    padding-right: 0;
    float: left;
}
.condition_name{
    font-size: 13px;
    line-height: 30px;
}
.addlink>.ant-modal-content,.addcontact>.ant-modal-content,.addbusiness>.ant-modal-content,.addchange>.ant-modal-content,.addincreased>.ant-modal-content,.addrecord>.ant-modal-content,.addcollection>.ant-modal-content{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
}
.addcontact>.ant-modal-title,.addlink>.ant-modal-title,.addbusiness>.ant-modal-title,.addchange>.ant-modal-title,.addincreased>.ant-modal-title,.addrecord>.ant-modal-title,.addcollection>.ant-modal-title{
    font-size: 18px;
}
.addcontact_input,.addlink_input,.addbusiness_input,.addchange_input,.addincreased_input,.addcollection_input{
    width: 300px;
    margin: -18px 0 0 -150px;
    position: absolute;
    left: 50%;
    top: 5%;
}
.addcontact_table,.addlink_table,.addbusiness_table,.addchange_table,.addincreased_table,.addcollection_table,.addrecord_table{
padding-top: 10px;
}
.addcontact_footer,.addlink_footer,.addbusiness_footer,.addchange_footer,.addincreased_footer,.addcollection_footer{
    height: 50px;
}
